// interfaces
import {Card, CardContent, CardHeader, Typography} from '@mui/material';
import {SelectedProgram} from '../interfaces';

interface ProgramDetailProps {
  selectedProgram: SelectedProgram | null;
}

const ProgramDetail = ({selectedProgram}: ProgramDetailProps) => {
  if (!selectedProgram) {
    return null;
  }

  const {title, description, since, till, duration} = selectedProgram;

  return (   
    <Card sx={{backgroundColor: '#282c34a2', backdropFilter: 'blur(2px)',maxHeight: '30vh'}}>
      <CardHeader title={title} subheader={`${since} - ${till} (${duration})`} />
      <CardContent>      
        <Typography variant='body2' color='text.secondary'>
          {description}
        </Typography>        
      </CardContent>
    </Card>
  );
};

export default ProgramDetail;
