import React from 'react';
import {formatTime} from '../utils';
import useTVChannels from './TVChannelsContextHook';
import {Typography} from '@mui/material';
import VolumeOSD from './VolumeOSD';

const OSD: React.FC = () => {
  const time = new Date();
  const {currentChannel, videoVolume} = useTVChannels();

  return (
    <div className='osd'>
      <div className='block left'>
        {currentChannel?.iconSrc && <img height={70} style={{maxWidth: '200px'}} src={currentChannel.iconSrc} alt='osd channel logo' />}
        {!currentChannel?.iconSrc && <Typography variant='h4'>{currentChannel?.name}</Typography>}
      </div>
      <div className='block right'>
        <Typography variant='h4'>{formatTime(time)}</Typography>
      </div>
      <VolumeOSD
        volume={videoVolume}
        style={{
          position: 'fixed',
          top: '45vh',
          left: 0,
        }}
      />
    </div>
  );
};

export default OSD;
