import React from 'react';
import ReactPlayer from 'react-player';
import TouchEventComponent from './TouchEventComponent';
import useTVChannels from './TVChannelsContextHook';

interface PlayerwWithTouchProps {
  url: string;
}

const PlayerwWithTouch: React.FC<PlayerwWithTouchProps> = ({url}) => {
  const {channels, isVideoMuted, isVideoPaused, videoVolume, setVideoVolume, setIsVideoPlaying, switchChannelById} = useTVChannels();

  const switchNextChannel = () => {
    const next = channels.getNextChannel();
    if (next) switchChannelById(next.id);
  };
  const switchPreviousChannel = () => {
    const next = channels.getPreviousChannel();
    if (next) switchChannelById(next.id);
  };
  return (
    <>
      <TouchEventComponent
        onSwipedLeft={() => switchNextChannel()}
        onSwipedRight={() => switchPreviousChannel()}
        onSwipedUp={() => setVideoVolume(videoVolume + 0.1)}
        onSwipedDown={() => setVideoVolume(videoVolume - 0.1)}>
        <ReactPlayer
          url={url}
          playing={!isVideoPaused}
          volume={videoVolume}
          onPlay={() => {
            setIsVideoPlaying(true);
          }}
          onPause={() => setIsVideoPlaying(false)}
          onEnded={() => setIsVideoPlaying(false)}
          onError={() => {
            setIsVideoPlaying(false);
          }}
          muted={isVideoMuted}
          pip={false}
          width='100%'
          height='100%'
          config={{
            file: {
              attributes: {
                disablePictureInPicture: true,
                controlsList: 'nodownload',
                playsInline: true,
              },
            },
          }}
          playsinline={true}
          className='mitsos'
        />
      </TouchEventComponent>
    </>
  );
};

export default PlayerwWithTouch;
