import React, {FC, useEffect} from 'react';
import {useRef} from 'react';
import ReactPlayer from 'react-player';
import {useIsVisible} from './useVisbile';

interface MiniPlayerProps {
  name?: string;
  url?: string;
}
const MiniPlayer: FC<MiniPlayerProps> = ({name, url}) => {
  const ref = useRef<any>();
  const isVisible = useIsVisible(ref);
  useEffect(() => {
    // if (isVisible ) console.log("Playing:",name||url)
    // else console.log("Stopped:",name||url);
  }, [isVisible, name, url]);
  return (
    <div ref={ref}>
      <ReactPlayer url={url || ''} playing={isVisible} muted={true} width='100%' height='100%' fileconfig={{attributes: {playsinline: true}}} playsinline={true} />
    </div>
  );
};
export default MiniPlayer;
