import {Epg as PlanbyEpg, Layout} from 'planby';
// hooks
import usePlanby from '../hooks/usePlanby';
// components
import Timeline from './Timeline';
import ChannelItem from './ChannelItem';
import ProgramItem from './ProgramItem';
import ProgramDetail from './ProgramDetail';

const Epg = () => {
  const {isLoading, debouncedSelectedProgram, setSelectedProgram, getEpgProps, getLayoutProps} = usePlanby();

  return (
    <div style={{height: 'calc(100vh)', width: 'calc(100vw - 30px)', position: 'absolute', top: '0', left: '0'}}>
      <ProgramDetail selectedProgram={debouncedSelectedProgram} />
      {/* <div style={{height: '30vh', margin: 0}}>
        <ProgramDetail selectedProgram={debouncedSelectedProgram} />
      </div>
      <div style={{height: '70vh'}}> */}
        <PlanbyEpg isLoading={isLoading} {...getEpgProps()}>
          <Layout
            {...getLayoutProps()}
            renderTimeline={(props) => <Timeline {...props} />}
            renderChannel={({channel}) => <ChannelItem key={channel.uuid} channel={channel} />}
            renderProgram={({program, ...rest}) => <ProgramItem key={program.data.id} program={program} selectedProgram={debouncedSelectedProgram} setSelectedProgram={setSelectedProgram} {...rest} />}
          />
        </PlanbyEpg>
      {/* </div> */}
    </div>
  );
};

export default Epg;
