import Program from './Program';

class Programmes {
  programs: Program[] = [];

  addProgram(program: Program): void {
    this.programs.push(program);
  }

  programByDate(date: Date): Program[] {
    return this.programs.filter((p) => p.inTime(date));
  }

  firstProgramByDate(date: Date): Program | null {
    const filtered = this.programByDate(date);
    return filtered.length > 0 ? filtered[0] : null;
  }

  currentProgramm(): Program | null {
    const now = new Date();
    return this.firstProgramByDate(now);
  }

  currentProgramms(): Program[] {
    const now = new Date();
    return this.programByDate(now);
  }
  getNextProgram(date: Date = new Date()): Program | null {
    const programsAfterGivenDate = this.programs.filter((p) => p.start > date);
    if (programsAfterGivenDate.length === 0) return null;
    return programsAfterGivenDate[0];
  }
  sorted(): Program[] {
    return this.programs.sort((a, b) => {
      if (a.start < b.start) {
        return -1;
      }
      if (a.start > b.start) {
        return 1;
      }
      return 0;
    });
  }
  sortProgrammes() {
    this.programs = this.sorted();
  }
  afterDate(date: Date = new Date()): Program[] {
    const idx = this.sorted().findIndex((p) => {
      return p.inTime(date);
    });
    return this.programs.slice(idx);
  }
  beforeDate(date: Date = new Date()): Program[] {
    const idx = this.sorted().findIndex((p) => {
      return p.inTime(date);
    });
    return this.programs.slice(0, idx);
  }
  


}

export default Programmes;
