import React from 'react';
import useTVChannels from './TVChannelsContextHook';
import Epg from '../module/planbyEPG/components/Epg';
const EPG: React.FC = () => {
  const {isLoading, isEPGOpen} = useTVChannels();
  if (isLoading || !isEPGOpen) return <></>;
  return (
    <>
      <Epg />
    </>
  );
};
export default EPG;
