import React from 'react';
import './App.css';
import TVChannels from './components/TVChannels';
import LoadingScreen from './components/LoadingScreen';
import {CssBaseline, ThemeProvider, createTheme, styled} from '@mui/material';
import useMediaQuery from '@mui/material/useMediaQuery';
import Main from './pages/Main';

const App: React.FC = () => {
  const searchParams = new URLSearchParams(document.location.search);
  let m3u8Url = searchParams.get('m3u8');
  if (!m3u8Url || !m3u8Url.length) {  
    m3u8Url = './iptv.m3u8';
  }
  let epgUrl = searchParams.get('epg');
  if (!epgUrl || !epgUrl.length) {  
    epgUrl = './epg.xml';
  }
  // const theme = useTheme();  
  const device={
    xs: useMediaQuery('(min-width:420px)'),
    sm: useMediaQuery('(min-width:600px)'),
    md: useMediaQuery('(min-width:1200px)'),
    ys: useMediaQuery('(min-width:400px)'),
    isLandscape: useMediaQuery('(orientation: landscape)'),
    isPortrait: useMediaQuery('(orientation: portrait)'),
    isMobile: useMediaQuery('(max-width:600px)'),
    isTablet: useMediaQuery('(max-width:900px)'),
    isDesktop: useMediaQuery('(min-width:900px)'),    
  }

  const GlobalStyle = styled('div')(({theme}) => ({
    '& .MuiCssBaseline-root': {
      backgroundColor: theme.palette.background.default,
      color: theme.palette.text.primary,
    },
    'html, body, #root, #app': {
      // Depending on the ID of your root div
      height: '100%',
      backgroundColor: theme.palette.background.default,
    },
  }));

  const theme = createTheme({
    palette: {
      mode: 'dark',
      background: {
        default: '#000', // Adjust this value as per your preference
        paper: '#171923', // Adjust this value as per your preference
      },
    },
    typography: {
      fontSize: device.xs ? (device.md ? 14 : 12) : 10,
    },
    components: {
      MuiUseMediaQuery: {
        defaultProps: {
          noSsr: true,
        },
      },
    },
  });

  return (
    <ThemeProvider theme={theme}>
      <LoadingScreen />
      <GlobalStyle />
      <CssBaseline />
      <TVChannels m3u8Url={m3u8Url} epgUrl={epgUrl} device={device} >
        <Main />
      </TVChannels>
    </ThemeProvider>
  );
};

export default App;
