import React, {createContext} from 'react';
import Channels from '../classes/Channels';
import Channel from '../classes/Channel';
import m3uparse from '../classes/M3u8';
import {Backdrop, CircularProgress} from '@mui/material';

// Define the shape of the context
type TVChannelsContextProps = {
  channels: Channels;
  currentChannel: Channel | null;
  isFullScreen: boolean;
  isChannelsListOpen: boolean;
  isEPGOpen: boolean;
  isVROpen: boolean;
  isOverlayPinned: boolean;
  isLoading: boolean;
  isOverlayShowing: boolean;
  isUserInactive: boolean;
  isVideoPlaying: boolean;
  isVideoMuted: boolean;
  isVideoPaused: boolean;
  videoVolume: number;
  device: {
    xs: boolean,
    sm: boolean,
    md: boolean,
    ys: boolean,
    isLandscape: boolean, 
    isPortrait: boolean,
    isMobile: boolean,
    isTablet: boolean,
    isDesktop: boolean,
  }
  setChannels: (channels: Channels) => void;
  setIsFullScreen: (fullScreen: boolean) => void;
  setIsChannelsListOpen: (ChannelsListOpen: boolean) => void;
  setIsEPGOpen: (EPGOpen: boolean) => void;
  setIsVROpen: (VROpen: boolean) => void;
  setIsLoading: (loading: boolean) => void;
  setIsOverlayShowing: (overlayShowing: boolean) => void;
  setIsOverlayPinned: (overlayPinned: boolean) => void;
  setIsUserInactive: (userInactive: boolean) => void;
  setIsVideoPlaying: (videoPlaying: boolean) => void;
  setIsVideoMuted: (videoMuted: boolean) => void;
  setIsVideoPaused: (videoPaused: boolean) => void;
  setVideoVolume: (videoVolume: number) => void;
  error: string | null;
  switchChannelById: (id: string) => void;
  switchChannelByName: (name: string) => void;
  addListener: (callback: (channel: Channel) => void) => void;
  removeListener: (callback: (channel: Channel) => void) => void;
};

export const TVChannelsContext = createContext<TVChannelsContextProps | undefined>(undefined);

type TVChannelsProps = {
  m3u8Url: string;
  epgUrl: string;
  children: React.ReactNode;
  device: {
    xs: boolean,
    sm: boolean,
    md: boolean,
    ys: boolean,
    isLandscape: boolean, 
    isPortrait: boolean,
    isMobile: boolean,
    isTablet: boolean,
    isDesktop: boolean,
  };  
};

class TVChannels extends React.Component<TVChannelsProps, any> {  
  private listeners: Array<(channel: Channel) => void> = [];
  constructor(props: TVChannelsProps) {
    super(props);

    this.state = {
      channels: null,
      currentChannel: null,
      isFullScreen: false,
      isChannelsListOpen: false,
      isEPGOpen: false,
      isVROpen: false,
      isLoading: true,
      isOverlayShowing: false,
      isOverlayPinned: false,
      isUserInactive: true,
      isVideoPlaying: false,
      isVideoMuted: false,
      isVideoPaused: false,
      videoVolume: 1,
      xs: false,
      sm: false,
      md: false,
      error: null,
    };
  }

  async componentDidMount() {
    const chans = await m3uparse(this.props.m3u8Url,this.props.epgUrl);
    this.setState({isLoading: false, channels: chans});
  }
  setChannels = (channels: Channels) => {
    this.setState({channels: channels});
  };
  setIsFullScreen = (fullScreen: boolean) => {
    this.setState({isFullScreen: fullScreen});
  };
  setIsChannelsListOpen = (ChannelsListOpen: boolean) => {
    this.setState({isChannelsListOpen: ChannelsListOpen});
  };
  setIsEPGOpen = (EPGOpen: boolean) => {
    this.setState({isEPGOpen: EPGOpen});
  };
  setIsVROpen = (VROpen: boolean) => {
    this.setState({isVROpen: VROpen});
  };

  setIsLoading = (loading: boolean) => {
    this.setState({isLoading: loading});
  };

  setIsOverlayShowing = (overlayShowing: boolean) => {
    this.setState({isOverlayShowing: overlayShowing});
  };
  setIsOverlayPinned = (overlayPinned: boolean) => {
    this.setState({isOverlayPinned: overlayPinned});
  };

  setIsUserInactive = (userInactive: boolean) => {
    this.setState({isUserInactive: userInactive});
  };

  setIsVideoPlaying = (videoPlaying: boolean) => {
    this.setState({isVideoPlaying: videoPlaying});
  };

  setIsVideoMuted = (videoMuted: boolean) => {
    this.setState({isVideoMuted: videoMuted});
  };
  setIsVideoPaused = (videoPaused: boolean) => {
    this.setState({isVideoPaused: videoPaused});
  };
  setVideoVolume = (videoVolume: number) => {
    if (videoVolume > 1) videoVolume = 1;
    if (videoVolume < 0) videoVolume = 0;
    if (videoVolume === 0 && !this.state.isVideoMuted) this.setIsVideoMuted(true);
    if (videoVolume > 0 && this.state.isVideoMuted) this.setIsVideoMuted(false);
    this.setState({videoVolume: videoVolume});
  };

  switchChannelById = (id: string) => {
    this.state.channels.setCurrentChannel(id);
    const newChannel = this.state.channels.getCurrentChannel();
    this.setState({currentChannel: newChannel});
    this.setIsOverlayShowing(true);
    this.setIsUserInactive(false);
    // Notify listeners about the channel switch
    this.listeners.forEach((callback) => callback(newChannel));
  };

  switchChannelByName = (name: string) => {
    const channel = this.state.channels.getAllChannels().find((ch: {name: string}) => ch.name === name);
    if (channel) {
      this.switchChannelById(channel.id);
    }
  };

  addListener = (callback: (channel: Channel) => void) => {
    this.listeners.push(callback);
  };
  removeListener = (callback: (channel: Channel) => void) => {
    this.listeners = this.listeners.filter((listener) => listener !== callback);
  };

  render() {
    return (
      <TVChannelsContext.Provider
        value={{
          channels: this.state.channels,
          currentChannel: this.state.currentChannel,
          isFullScreen: this.state.isFullScreen,
          isChannelsListOpen: this.state.isChannelsListOpen,
          isEPGOpen: this.state.isEPGOpen,
          isVROpen: this.state.isVROpen,
          isLoading: this.state.isLoading,
          isOverlayShowing: this.state.isOverlayShowing,
          isOverlayPinned: this.state.isOverlayPinned,
          isUserInactive: this.state.isUserInactive,
          isVideoPlaying: this.state.isVideoPlaying,
          isVideoMuted: this.state.isVideoMuted,
          isVideoPaused: this.state.isVideoPaused,
          videoVolume: this.state.videoVolume,
          error: this.state.error,
          device: this.props.device,          
          setChannels: this.setChannels,
          setIsFullScreen: this.setIsFullScreen,
          setIsChannelsListOpen: this.setIsChannelsListOpen,
          setIsEPGOpen: this.setIsEPGOpen,
          setIsVROpen: this.setIsVROpen,
          setIsLoading: this.setIsLoading,
          setIsOverlayShowing: this.setIsOverlayShowing,
          setIsOverlayPinned: this.setIsOverlayPinned,
          setIsUserInactive: this.setIsUserInactive,
          setIsVideoPlaying: this.setIsVideoPlaying,
          setIsVideoMuted: this.setIsVideoMuted,
          setIsVideoPaused: this.setIsVideoPaused,
          setVideoVolume: this.setVideoVolume,
          switchChannelById: this.switchChannelById,
          switchChannelByName: this.switchChannelByName,
          addListener: this.addListener,
          removeListener: this.removeListener,
        }}>
        <Backdrop sx={{color: '#fff'}} open={this.state.isLoading}>
          <CircularProgress color='inherit' />
        </Backdrop>
        {!this.state.isLoading && this.props.children}
      </TVChannelsContext.Provider>
    );
  }
}

export default TVChannels;
