import {parse} from 'iptv-playlist-parser';
import Channels from './Channels';
import Channel from './Channel';
import EpgParser from './EpgParser';

class M3U8 {
  m3u8url: string;
  epgurl: string;
  channels: Channels = new Channels();

  constructor(m3u8Url: string,epgUrl: string) {
    this.m3u8url = m3u8Url;
    this.epgurl = epgUrl;
  }
  async loadAndParse(): Promise<any> {
    try {
      const response = await fetch(this.m3u8url);
      const content = await response.text();
      const result = parse(content);
      const extractedEpgUrl=this.extractEpgUrl(content);
      const epgUrl = this.epgurl && this.epgurl.length ? this.epgurl :( extractedEpgUrl && extractedEpgUrl.length ? extractedEpgUrl : null);

      result.items.forEach((item) => {
        // console.log("parse item:",item);
        const channel = new Channel(
          item.name, // id, you might want a more unique id system
          item.name ? item.name : item.tvg?.name,
          item.tvg?.name.length ? item.tvg?.name : item.name, // name
          item.tvg.logo || null, // iconSrc
          item.url || null // streamUrl
        );
        this.channels.addChannel(channel);
      });

      if (epgUrl) {
        const epgParser = new EpgParser(epgUrl, this.channels);
        await epgParser.mapChannelsProgrammes();

        // // Here you'd merge or update the channels in this.channels with the data from parsedEpgChannels.
        // // This can be done in multiple ways based on what you want (update icons, add programs, etc.)
        // // I'll demonstrate a basic example:
        // //console.log("0Matching:",parsedEpgChannels,parsedEpgChannels.getAllChannels());
        // parsedEpgChannels.getAllChannels().forEach(epgChannel => {
        //     const matchingChannel = this.channels.getChannelById(epgChannel.id);
        //     console.log("1Matching...",epgChannel.id,this.channels,matchingChannel);
        //     if (matchingChannel) {
        //         matchingChannel.iconSrc = epgChannel.iconSrc; // Update the icon
        //         matchingChannel.programmes = epgChannel.programmes; // Update the programs
        //    //     console.log("Matching...",matchingChannel.programmes);
        //     }
        // });
      }
    } catch (error) {
      console.error('Error loading and parsing m3u8:', error);
    }
  }

  extractEpgUrl(content: string): string | null {
    const match = content.match(/url-tvg="([^"]+)"/);
    return match ? match[1] : null;
  }
}

const m3uparse = async (m3u8url: string,epgurl: string) => {
  // console.log("parsing m3u:",url);
  var cChannels=new Channels();
  if (!cChannels.fromCache()){
    const m3u = new M3U8(m3u8url,epgurl);
    await m3u.loadAndParse();
    cChannels=m3u.channels;
    cChannels.toCache();
  }
  cChannels.sortPrograms();
  cChannels.updatePrograms();
  cChannels.startProgramScheduler();
  //console.log("channels:",cChannels);
  return cChannels;
};

export default m3uparse;
