// utils.ts or utils.tsx
import i18n from 'i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import moment from 'moment';

// i18next setup
i18n
.use(LanguageDetector) // Automatically determine user's language
.init({
  // fallback language if not provided
  fallbackLng: 'en',

  // resources defines your translation json files
  resources: {
    en: {
      translation: {
        hours: 'h',
        minutes: 'm',
      },
    },
    de: {
      translation: {
        hours: 'Std',
        minutes: 'Min',
      },
    },
    el: {
      // Greek
      translation: {
        hours: 'ω',
        minutes: 'λ',
      },
    },
    it: {
      // Italian
      translation: {
        hours: 'h',
        minutes: 'm',
      },
    },
    fr: {
      // French
      translation: {
        hours: 'h',
        minutes: 'min',
      },
    },
    nl: {
      // Dutch
      translation: {
        hours: 'u',
        minutes: 'min',
      },
    },
    es: {
      // Spanish
      translation: {
        hours: 'h',
        minutes: 'min',
      },
    },
    // ... and so on for other languages
  },
});

i18n.on('languageChanged', function (lng) {
  moment.locale(lng);
});

export function langs() {
  return Array.from(new Set(i18n.languages.map((l) => l.substring(0, 2))));
}
export function i18() {
  return i18n;
}

// console.log("ulangs:",langs(),i18n.languages,i18n.language,i18n.resolvedLanguage);

type FetchFunction = () => Promise<any>;
const MAX_RETRIES = 3;

export async function fetchDataWithRetry(fetchFunction: FetchFunction, retries = MAX_RETRIES): Promise<any> {
  for (let i = 0; i < retries; i++) {
    try {
      return await fetchFunction();
    } catch (error) {
      if (i === retries - 1) {
        throw error;
      }
    }
  }
}

type CacheEntry = {dt:Date,data:any};

export function saveToCache(key:string,data: any): void {
  try {
    const c:CacheEntry={dt:new Date(),data:data};
    localStorage.setItem(key, JSON.stringify(c));
  } catch(e) {
    console.log("error saving to cache:",e);
  }
}

export function loadFromCache(key:string,lifetime:number): any {
  const searchParams = new URLSearchParams(document.location.search);
  let nocache = searchParams.get('nocache');
  if (nocache) {
    console.log("nocache:",nocache);
    return null;
  }
  try{
    const cachedData = localStorage.getItem(key);
    if (cachedData) {
      const parsed=JSON.parse(cachedData) as CacheEntry;
      if (!parsed.dt || !parsed.data){
        localStorage.removeItem(key);
        console.log("malformed cache data key:",key," removed.");
        return null;
      }
      parsed.dt=new Date(parsed.dt);
     // console.log("cache dt:",parsed.dt);
      if(parsed.dt.getTime()+lifetime*1000<new Date().getTime()) {
        localStorage.removeItem(key);
        console.log("stale cache data key:",key," removed.");
        return null;
      } else {
       // console.log("cache data key:",key," served.");
        return parsed.data;
      }
    }  
  } catch(e) {
    console.log("malformed cache data key:",key," removed.");
    localStorage.removeItem(key);
    return null;
  }
}
export function formatTime(date?: Date) {
  if (!date) return '';
  let hours = date.getHours();
  let minutes = date.getMinutes();

  // Pad single-digit hours and minutes with a leading zero
  let formattedHours = hours.toString().padStart(2, '0');
  let formattedMinutes = minutes.toString().padStart(2, '0');

  return `${formattedHours}:${formattedMinutes}`;
}
export function calcDurationPerc(start: Date, stop?: Date) {
  const currentTime = new Date();
  // Calculate total duration (in milliseconds)
  if (!stop) return 0;

  const totalDuration = stop.getTime() - start.getTime();

  // Calculate elapsed time (in milliseconds)
  const elapsedTime = currentTime.getTime() - start.getTime();

  // Calculate progress percentage
  let progressPercentage = (elapsedTime / totalDuration) * 100;

  // Ensure the percentage doesn't exceed 100% (it can in edge cases)
  progressPercentage = Math.min(progressPercentage, 100);
  return progressPercentage;
}

export function remainingTime(end?: Date): string {
  if (!end) {
    return '∞';
  }
  // Calculate the difference in milliseconds using current date as start
  const diff = moment(end).diff(moment());

  // Convert the difference to hours and minutes
  const duration = moment.duration(diff);
  const hours = duration.hours();
  const minutes = duration.minutes();

  // Format the result
  let result = '';
  if (hours > 0) {
    result += `${hours}${i18n.t('hours')}`;
  }
  if (minutes > 0 || !result) {
    // if no hours, append minutes regardless
    result += `${minutes || 0}${i18n.t('minutes')}`;
  }

  return result;
}
export function duration(start: Date, end?: Date): string {
  if (!end) {
    return '∞';
  }
  // Calculate the difference in milliseconds using current date as start
  const diff = moment(end).diff(moment(start));

  // Convert the difference to hours and minutes
  const duration = moment.duration(diff);
  const hours = duration.hours();
  const minutes = duration.minutes();

  // Format the result
  let result = '';
  if (hours > 0) {
    result += `${hours}${i18n.t('hours')}`;
  }
  if (minutes > 0 || !result) {
    // if no hours, append minutes regardless
    result += `${minutes || 0}${i18n.t('minutes')}`;
  }

  return result;
}
