// @ts-ignore
import {XmltvChannel, XmltvProgramme, Xmltv, parseXmltv} from '@iptv/xmltv';
import Channels from './Channels';
import Program from './Program';
import ChannelMatcher from './ChannelMatcher';
import Channel from './Channel';

class EpgParser {
  private epgUrl: string;
  private channels: Channels;

  constructor(epgUrl: string, channels: Channels) {
    this.epgUrl = epgUrl;
    this.channels = channels;
  }

  async fetchAndParse(): Promise<{lchannels: Channel[], lprograms: Program[] | undefined}> {
    try {
      const response = await fetch(this.epgUrl);
      const xmlData = await response.text();
      const parsedData = parseXmltv(xmlData) as Xmltv;

      if (!parsedData) {
        throw new Error('Failed to parse XMLTV data: Root error.');
      }

      const lchannels = parsedData.channels?.map((channel: XmltvChannel) => 
        new Channel( channel.id, channel.displayName?.[0]?._value || 'Unknown','', channel.icon?.[0]?.src || null)
      ) as Channel[];
      //   {
      //   id: channel.id,
      //   name: channel.displayName?.[0]?._value || 'Unknown',
      //   icon: channel.icon?.[0]?.src || null,
      // }));
      const lprograms = parsedData.programmes?.map((program: XmltvProgramme) =>new Program(program));

      return {
        lchannels,
        lprograms,
      };
    } catch (error) {
      console.error('Error fetching and parsing EPG data:', error);
      throw error;
    }
  }

  async mapChannelsProgrammes(): Promise<any> {    
    
    await this.fetchAndParse().then((epg) => {
        
    const matcher = new ChannelMatcher(epg.lchannels, this.channels.getAllChannels());
    const res=matcher.newMatcher(90);
    //console.log(res);
    
    res.forEach((r)=>{
      const tchannel=this.channels.getChannelByIndex(r.targetIdx)
      if (tchannel) {
        //console.log(tchannel.iconSrc,r.source.iconSrc);
        if (r.source.iconSrc && !tchannel.iconSrc ) {
          //r.source.iconSrc = tchannel.iconSrc;               
          tchannel.iconSrc = r.source.iconSrc;               
        }      
        tchannel.id = r.source.id;
        epg.lprograms?.filter((p)=>p.channel===r.source.id).forEach(p=>tchannel.schedules.addProgram(p))        
      }
    })    
      // epg.lchannels?.forEach((channelData: Channel) => {
      //   const fchannel = this.channels.getChannelByTVGName(channelData.name);
      //   if (fchannel) {
      //     if (!fchannel.iconSrc) fchannel.iconSrc = channelData.iconSrc;
      //     fchannel.id = channelData.id;
      //   }
      // });

      // Populate programs for each channel
      // epg.lprograms?.forEach((program) => {
      //   //const program = new Program(programData);
      //   this.channels.getChannelById(program.channel)?.schedules.addProgram(program);
      // });
    });
    // console.log(this.channels);
    // this.channels.sortPrograms();
    // this.channels.updatePrograms();
    // this.channels.startProgramScheduler();
  }
}

export default EpgParser;
