import {ProgramItem as ProgramItemType, ProgramBox, ProgramContent, ProgramFlex, ProgramStack, ProgramTitle, ProgramText, useProgram} from 'planby';
// interfaces
import {SelectedProgram} from '../interfaces';
import { Tooltip, TooltipProps, styled, tooltipClasses } from '@mui/material';

interface ProgramItemProps extends ProgramItemType {
  selectedProgram:SelectedProgram | null;
  setSelectedProgram: (selectedProgram: SelectedProgram | null) => void;
}
const HtmlTooltip = styled(({ className, ...props }: TooltipProps) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: '#282c34a2',
    backdropFilter: 'blur(2px)',
    color: '#ffffff',
    // maxWidth: 220,
    fontSize: theme.typography.pxToRem(12),
    border: '1px solid #3850af',
  },
}));
const ProgramItem = ({program, selectedProgram, setSelectedProgram, ...rest}: ProgramItemProps) => {
  const {styles, formatTime, set12HoursTimeFormat, isLive} = useProgram({
    program,
    ...rest,
  });

  const {data} = program;
  const {title, description, 
    // duration, 
    since, till} = data;

  const sinceTime = formatTime(since, set12HoursTimeFormat()).toLowerCase();
  const tillTime = formatTime(till, set12HoursTimeFormat()).toLowerCase();

  // const handleClick = () => {    
  //   if (selectedProgram !== null) setSelectedProgram(null);
  //   else
  //   setSelectedProgram({
  //     title,
  //     description,
  //     since: sinceTime,
  //     till: tillTime,
  //     duration,
  //   });
  // };

 

  return (
    <ProgramBox width={styles.width} style={styles.position} /*onClick={handleClick}*/>
      <ProgramContent width={styles.width} isLive={isLive}>
        <ProgramFlex>
        <HtmlTooltip title= {`${description}`}>
          <ProgramStack>            
              <ProgramTitle>{title}</ProgramTitle>
              <ProgramText>
                {sinceTime} - {tillTime}
              </ProgramText>
          </ProgramStack> 
          </HtmlTooltip>                   
        </ProgramFlex>
      </ProgramContent>
    </ProgramBox>
  );
};

export default ProgramItem;
