import useMediaQuery from '@mui/material/useMediaQuery';
import {Channel, ChannelBox, ChannelLogo} from 'planby';
import useTVChannels from '../../../components/TVChannelsContextHook';

interface ChannelItemProps {
  channel: Channel;
}

const ChannelItem = ({channel}: ChannelItemProps) => {
  const {setIsEPGOpen,switchChannelById} = useTVChannels();
  const {title, position, logo} = channel;
  const sm = useMediaQuery('(min-width:600px)');
  return (
    <ChannelBox {...position} style={{maxWidth: sm ? '' : '50px'}}>
      <ChannelLogo src={logo} alt={title} style={{maxHeight: 50, maxWidth: sm? 80:30,cursor:'pointer'}} onClick={()=>{switchChannelById(channel.uuid);setIsEPGOpen(false);}}/>
    </ChannelBox>
  );
};

export default ChannelItem;
