import React from 'react';
import {makeStyles, withStyles} from '@mui/styles';
import LinearProgress from '@mui/material/LinearProgress';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import VolumeDownIcon from '@mui/icons-material/VolumeDown';
import VolumeOffIcon from '@mui/icons-material/VolumeOff';

const BorderLinearProgress = withStyles((theme) => {
  return {
    root: {
      borderRadius: 2,
      width: 2,
      height: '100%',
    },
    bar: {
      borderRadius: 2,
      transform: ({value}) => {
        return `translateY(${value}%) !important`;
      },
    },
  };
})(LinearProgress);

const useStyles = makeStyles({
  stats: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    height: '10vh',
  },
});
interface VolumeOSDProps {
  volume: number;
  style?: React.CSSProperties;
}

const VolumeOSD: React.FC<VolumeOSDProps> = ({volume, style}) => {
  const classes = useStyles();

  return (
    <div className={classes.stats} style={style}>
      {volume === 0 && <VolumeOffIcon fontSize='small' />}
      {volume === 1 && <VolumeUpIcon fontSize='small' />}
      {volume > 0 && volume < 1 && <VolumeDownIcon fontSize='small' />}
      <BorderLinearProgress variant='determinate' value={100 - volume * 100} />
    </div>
  );
};
export default VolumeOSD;
