import Program from './Program';
import { loadFromCache, saveToCache } from '../utils';
import Channel from './Channel';

type ChannelSwitchCallback = (newChannel: Channel) => void;

class Channels {
  private channelList: Channel[] = [];
  private currentChannelIndex: number = 0; // Default to the first channel
  private onChannelSwitch?: ChannelSwitchCallback;
  private timer?: NodeJS.Timeout;

  constructor(onChannelSwitch?: ChannelSwitchCallback) {
    this.onChannelSwitch = onChannelSwitch;
  }
  startProgramScheduler() {
    this.scheduleNextUpdate();
  }

  private scheduleNextUpdate() {
    const now = new Date();
    let smallestDelay: number | null = null;
    let channelsToUpdate: Channel[] = [];
    // console.log('Timer Scheduler start');
    this.channelList.forEach((channel) => {
      const upcomingPrograms = channel.schedules.programs.filter((program) => program.start > now);
      if (upcomingPrograms.length) {
        const nextProgramStartTime = upcomingPrograms[0].start;
        const delayForThisChannel = nextProgramStartTime.getTime() - now.getTime();
        // console.log('Timer Scheduler, channel',channel.name,', next start:',nextProgramStartTime,', delay:',delayForThisChannel/1000,' sec, upcomming programs:',upcomingPrograms,);
        if (smallestDelay === null || delayForThisChannel < smallestDelay) {
          smallestDelay = delayForThisChannel;
          channelsToUpdate = [channel];
          // console.log('hit secs:',smallestDelay/1000);
        } else if (delayForThisChannel === smallestDelay) {
          channelsToUpdate.push(channel);
        }
      }
    });
    if (smallestDelay !== null) {
      // console.log('Timer Scheduler set:',smallestDelay/1000,' for ',channelsToUpdate);
      this.timer = setTimeout(
        () => {
          // console.log('Timer:',channelsToUpdate);
          channelsToUpdate.forEach((channel) => {
            channel.updateCurrentProgram();
          });
          this.scheduleNextUpdate(); // Once updated, recalculate the next update.
        },
        smallestDelay < 1000 ? 1000 : smallestDelay
      );
    } else {
      // If no upcoming programs were found, maybe check in a default time like an hour later
      this.timer = setTimeout(() => {
        // console.log('fallback timer enabled');
        this.scheduleNextUpdate();
      }, 3600000); // e.g., every 1 hour
    }
  }

  stopProgramScheduler() {
    if (this.timer) {
      clearTimeout(this.timer);
      this.timer = undefined;
    }
  }
  // Add a new channel to the collection
  addChannel(channel: Channel): void {
    //console.log("addChannel:",channel);
    this.channelList.push(channel);
  }

  // Get a channel by its ID
  getChannelById(id: string): Channel | null {
    return this.channelList.find((channel) => channel.id === id) || null;
  }  
  // Get a channel by its Name
  getChannelByName(name: string): Channel | null {
    return this.channelList.find((channel) => channel.name.toLowerCase() === name.toLowerCase()) || null;
  }
  // Get a channel by its Name
  getChannelByTVGName(name: string): Channel | null {
    return this.channelList.find((channel) => channel.tvgname.toLowerCase() === name.toLowerCase()) || null;
  }
  // Remove a channel by its ID
  removeChannelById(id: string): void {
    const index = this.channelList.findIndex((channel) => channel.id === id);
    if (index !== -1) {
      this.channelList.splice(index, 1);
    }
  }

  // Set a channel as the current channel
  setCurrentChannel(id: string): void {
    const index = this.channelList.findIndex((channel) => channel.id === id);
    if (index !== -1 && index !== this.currentChannelIndex) {
      this.currentChannelIndex = index;
      if (this.onChannelSwitch) {
        this.onChannelSwitch(this.channelList[this.currentChannelIndex]);
      }
    }
  }

  // Retrieve the current channel
  getCurrentChannel(): Channel | null {
    return this.channelList[this.currentChannelIndex] || null;
  }

  // Get the next channel after the current one
  getNextChannel(): Channel | null {
    if (this.currentChannelIndex < this.channelList.length - 1) {
      this.currentChannelIndex++;
      return this.channelList[this.currentChannelIndex];
    }
    return null; // Return null if we're at the last channel
  }

  // Get the previous channel before the current one
  getPreviousChannel(): Channel | null {
    if (this.currentChannelIndex > 0) {
      this.currentChannelIndex--;
      return this.channelList[this.currentChannelIndex];
    }
    return null; // Return null if we're at the first channel
  }
  // Search channels by name
  searchChannels(query: string): Channel[] {
    const lowercasedQuery = query.toLowerCase();
    return this.channelList.filter((channel) => channel.name.toLowerCase().includes(lowercasedQuery));
  }

  // Get all channels
  getAllChannels(): Channel[] {
    return this.channelList;
  }

  // Get a channel by its index
  getChannelByIndex(index: number): Channel | null {
    return this.channelList[index] || null;
  }

  // Get the total number of channels
  getTotalChannels(): number {
    return this.channelList.length;
  }

  // Iterate to the next channel
  iterateNext(): Channel | null {
    if (this.currentChannelIndex < this.channelList.length - 1) {
      this.currentChannelIndex++;
    }
    return this.getCurrentChannel();
  }

  // Iterate to the previous channel
  iteratePrevious(): Channel | null {
    if (this.currentChannelIndex > 0) {
      this.currentChannelIndex--;
    }
    return this.getCurrentChannel();
  }
  //updates all the channels program
  sortPrograms() {
    this.channelList.forEach((ch) => {
      ch.schedules.sortProgrammes();
    });
  }
  //updates all the channels program
  updatePrograms() {
    this.channelList.forEach((ch) => {
      ch.updateCurrentProgram();
    });
  }
  getProgramForAllChannels() {
    return this.channelList.flatMap((chan) => {
      return chan.schedules.programs;
    });
  }
  getEPG() {
    return this.getProgramForAllChannels().map((j, index) => {
      const p = j.getProgramInfo();
      return {
        id: index.toString(),
        description: p.desc,
        title: p.title,
        since: j.start,
        till: j.stop,
        channelUuid: j.channel,
        //image:"https://www.themoviedb.org/t/p/w1066_and_h600_bestv2/sjx6zjQI2dLGtEL0HGWsnq6UyLU.jpg",
        //isYesterday:true,
        // country:"Ghana",
        // Year:"2021–",
        // Rated:"TV-14",
        // Released:"29 Dec 2021",
        // Runtime:"N/A",
        // Genre:"Action, Adventure, Sci-Fi",
        // Director:"N/A",
        // Writer:"Jon Favreau",
        // Actors:"Temuera Morrison, Ming-Na Wen, Matt Berry",
        // Language:"English",
        // Country:"United States",
        // Awards:"N/A",
        // Metascore:"N/A",
        // imdbRating:"8.0",
        // imdbVotes:"20,147",
        // imdbID:"tt13668894",
        // Type:"series",
        // totalSeasons:"1",
        // Response:"True",
        // Ratings:[{"Source":"Internet Movie Database","Value":"8.0/10"}],
        // rating:3
      } as unknown;
    });
  }
  toCache() {
    saveToCache('channels', this.channelList);
  };
  fromCache() {
    var c=loadFromCache('channels', 60*60*24);
    if (!c) return false;
    c=c as Channel[];
    c.forEach((channel:Channel)=>{
      const lChannel=new Channel(channel.id,channel.name,channel.tvgname,channel.iconSrc,channel.streamUrl);
      channel.schedules.programs.forEach((program)=>{               
        const p=new Program(program);
        if (typeof p.start === 'string')
          p.start = new Date(p.start);
        if (typeof p.stop === 'string')
          p.stop = new Date(p.stop);        
        lChannel.schedules.addProgram(p);
      });      
      this.addChannel(lChannel);
    })    
    return true;
  }  
}
export default Channels;
