import React, {useState, useRef, ReactNode} from 'react';

interface TouchEventComponentProps {
  children?: ReactNode;
  onSwiped?: () => void;
  onSwipedLeft?: () => void;
  onSwipedRight?: () => void;
  onSwipedUp?: () => void;
  onSwipedDown?: () => void;
}

const TouchEventComponent: React.FC<TouchEventComponentProps> = ({children, onSwiped, onSwipedLeft, onSwipedRight, onSwipedUp, onSwipedDown}) => {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [swiped, setSwiped] = useState<boolean>(false);
  const swipe = useRef<{x?: number; y?: number; swiping?: boolean}>({});
  const minDistance = 90;

  const onTouchStart = (e: React.TouchEvent<HTMLDivElement>) => {
    const touch = e.touches[0];
    swipe.current = {x: touch.clientX, y: touch.clientY};
    setSwiped(false);
  };

  const onTouchMove = (e: React.TouchEvent<HTMLDivElement>) => {
    if (e.changedTouches && e.changedTouches.length) {
      swipe.current.swiping = true;
    }
  };

  const onTouchEnd = (e: React.TouchEvent<HTMLDivElement>) => {
    const touch = e.changedTouches[0];
    const x = touch.clientX - (swipe.current.x || 0);
    const y = touch.clientY - (swipe.current.y || 0);
    const absX = Math.abs(x);
    const absY = Math.abs(y);
    if (swipe.current.swiping) {
      if (absX > minDistance || absY > minDistance) {
        onSwiped && onSwiped();
        setSwiped(true);
      }
      if (absX > minDistance) {
        if (x > 0) onSwipedLeft && onSwipedLeft();
        else onSwipedRight && onSwipedRight();
      }
      if (absY > minDistance) {
        if (y > 0) onSwipedDown && onSwipedDown();
        else onSwipedUp && onSwipedUp();
      }
    }
    swipe.current = {};
  };

  return (
    <div onTouchStart={onTouchStart} onTouchMove={onTouchMove} onTouchEnd={onTouchEnd} style={{width: '100%', height: '100%'}}>
      {children}
    </div>
  );
};

export default TouchEventComponent;
