import React, {useEffect} from 'react';
import useTVChannels from './TVChannelsContextHook';
import {Stack, IconButton} from '@mui/material';
import {LiveTvTwoTone as LiveTv, DvrTwoTone as Dvr, ViewModuleTwoTone as ViewModule, PlayArrowTwoTone as PlayArrow, PauseTwoTone as Pause, SkipNextTwoTone as SkipNext, SkipPreviousTwoTone as SkipPrevious, VolumeOffTwoTone as VolumeOff, VolumeUpTwoTone as VolumeUp, FullscreenTwoTone as Fullscreen} from '@mui/icons-material';

const Navigation: React.FC = () => {
  const {
    isChannelsListOpen,
    isEPGOpen,
    isVROpen,
    isUserInactive,
    isVideoPaused,
    isVideoMuted,
    channels,
    device,
    setIsEPGOpen,
    setIsVROpen,
    setIsChannelsListOpen,
    setIsVideoPaused,
    setIsVideoMuted,
    switchChannelById,
    setIsFullScreen,
  } = useTVChannels();
  const switchNextChannel = () => {
    const next = channels.getNextChannel();
    if (next) switchChannelById(next.id);
  };
  const switchPreviousChannel = () => {
    const next = channels.getPreviousChannel();
    if (next) switchChannelById(next.id);
  };

  const handleFullscreenToggle = () => {
    if (!document.fullscreenElement) {
      if (document.documentElement.requestFullscreen) {
        document.documentElement.requestFullscreen().then(() => {
          setIsFullScreen(true);
        });
      }
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen().then(() => {
          setIsFullScreen(false);
        });
      }
    }
  };
  useEffect(() => {
    const onFullScreenChange = () => {
      setIsFullScreen(!!document.fullscreenElement);
    };

    document.addEventListener('fullscreenchange', onFullScreenChange);

    return () => {
      document.removeEventListener('fullscreenchange', onFullScreenChange);
    };
  }, [setIsFullScreen]);

  const btnSz = device.isMobile && device.isLandscape ? 'large' : 'small';
  if (isChannelsListOpen || isUserInactive) return <></>;

  return (
    <Stack
      direction='column'
      sx={{
        position: 'fixed',
        paddingTop: 'calc(50vh - 175px)',
        top: 0,
        right: 0,
        zIndex: 10,
        background: 'linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.7) 100%)',
        // backdropFilter: "blur(1px)",
        height: '100%',
      }}>
      <IconButton
        size={btnSz}
        onClick={() => {
          setIsChannelsListOpen(!isChannelsListOpen);
        }}>
        <LiveTv />
      </IconButton>
      <IconButton
        size={btnSz}
        onClick={() => {
          setIsVROpen(!isVROpen);
        }}>
        <ViewModule />
      </IconButton>
      <IconButton
        size={btnSz}
        onClick={() => {
          setIsEPGOpen(!isEPGOpen);
        }}>
        <Dvr />
      </IconButton>
      <IconButton
        size={btnSz}
        onClick={() => {
          setIsVideoPaused(!isVideoPaused);
        }}>
        {!isVideoPaused ? <Pause /> : <PlayArrow />}
      </IconButton>
      <IconButton
        size={btnSz}
        onClick={() => {
          switchNextChannel();
        }}>
        <SkipNext />
      </IconButton>
      <IconButton
        size={btnSz}
        onClick={() => {
          switchPreviousChannel();
        }}>
        <SkipPrevious />
      </IconButton>
      <IconButton
        size={btnSz}
        onClick={() => {
          setIsVideoMuted(!isVideoMuted);
        }}>
        {isVideoMuted ? <VolumeOff /> : <VolumeUp />}
      </IconButton>
      <IconButton
        size={btnSz}
        onClick={() => {
          handleFullscreenToggle();
        }}>
        <Fullscreen />
      </IconButton>
    </Stack>
  );
};

export default Navigation;
