import * as React from 'react';
import LinearProgress, {LinearProgressProps} from '@mui/material/LinearProgress';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';

function Progress(props: LinearProgressProps & {value: number; start?: string; label?: string; color?: LinearProgressProps['color']}) {
  return (
    <Box sx={{display: 'flex', alignItems: 'center', textAlign: 'center', flexBasis: '100%'}}>
      {props.start && (
        <Box sx={{minWidth: 60, textAlign: 'left'}}>
          <Typography variant='subtitle1' color='text.primary'>
            {props.start}
          </Typography>
        </Box>
      )}
      <Box sx={{width: '100%'}}>
        <LinearProgress variant='determinate' {...props} />
      </Box>
      {props.label && (
        <Box sx={{minWidth: 55, textAlign: 'right'}}>
          <Typography variant='subtitle1' color='text.primary'>
            {props.label}
          </Typography>
        </Box>
      )}
    </Box>
  );
}

export default Progress;
