import {
  XmltvAudio,
  XmltvCredits,
  XmltvEpisodeNumber,
  XmltvIcon,
  XmltvImage,
  XmltvLength,
  XmltvPreviouslyShown,
  XmltvProgramme,
  XmltvRating,
  XmltvReview,
  XmltvStarRating,
  XmltvSubtitle,
  XmltvUrl,
  XmltvVideo,
} from '@iptv/xmltv';
import {langs, i18} from '../utils';
const langIndex = require('country-data').languages;

class Program implements XmltvProgramme {
  channel!: string;
  title!: {_value: string; lang?: string | undefined}[];
  start!: Date;
  stop?: Date | undefined;
  pdcStart?: Date | undefined;
  vpsStart?: Date | undefined;
  showview?: string | undefined;
  videoplus?: string | undefined;
  clumpidx?: string | undefined;
  subTitle?: {_value: string; lang?: string | undefined}[] | undefined;
  desc?: {_value: string; lang?: string | undefined}[] | undefined;
  credits?: XmltvCredits | undefined;
  date?: Date | undefined;
  category?: {_value: string; lang?: string | undefined}[] | undefined;
  keyword?: {_value: string; lang?: string | undefined}[] | undefined;
  language?: {_value: string; lang?: string | undefined} | undefined;
  origLanguage?: {_value: string; lang?: string | undefined} | undefined;
  length?: XmltvLength | undefined;
  icon?: XmltvIcon[] | undefined;
  url?: XmltvUrl[] | undefined;
  country?: {_value: string; lang?: string | undefined} | undefined;
  episodeNum?: XmltvEpisodeNumber[] | undefined;
  video?: XmltvVideo | undefined;
  audio?: XmltvAudio | undefined;
  previouslyShown?: XmltvPreviouslyShown | undefined;
  premiere?: boolean | {_value: string; lang?: string | undefined} | undefined;
  lastChance?: boolean | {_value: string; lang?: string | undefined} | undefined;
  new?: boolean | undefined;
  subtitles?: XmltvSubtitle[] | undefined;
  rating?: XmltvRating[] | undefined;
  starRating?: XmltvStarRating[] | undefined;
  review?: XmltvReview[] | undefined;
  image?: XmltvImage[] | undefined;

  constructor(data: XmltvProgramme) {
    // if (data) {
    //   Object.assign(this, data);
    // }
  
    this.channel=data.channel;
    this.title=data.title;
    this.start=data.start;
    this.stop=data.stop;
    this.pdcStart=data.pdcStart;
    this.vpsStart=data.vpsStart;
    this.showview=data.showview;
    this.videoplus=data.videoplus;
    this.clumpidx=data.clumpidx;
    this.subTitle=data.subTitle;
    this.desc=data.desc;
    this.credits=data.credits;
    this.date=data.date;
    this.category=data.category;
    this.keyword=data.keyword;
    this.language=data.language;
    this.origLanguage=data.origLanguage;
    this.length=data.length;
    this.icon=data.icon;
    this.url=data.url;
    this.country=data.country;
    this.episodeNum=data.episodeNum;
    this.video=data.video;
    this.audio=data.audio;
    this.previouslyShown=data.previouslyShown;
    this.premiere=data.premiere;
    this.lastChance=data.lastChance;
    this.new=data.new;
    this.subtitles=data.subtitles;
    this.rating=data.rating;
    this.starRating=data.starRating;
    this.review=data.review;
    this.image=data.image;
    this.fixCountryCodes();
    this.title = this.programFilterLangs(this.title) || [];
    this.desc = this.programFilterLangs(this.desc);
    this.subTitle = this.programFilterLangs(this.subTitle);
  }

  inTime(date: Date): boolean {
    if (!this.stop) return date >= this.start;
    return date >= this.start && date <= this.stop;
  }

  isCurrent(): boolean {
    const now = new Date();
    return this.inTime(now);
  }
  private progEach(elm: typeof this.desc) {
    elm?.forEach((p) => {
      if (p.lang) {
        const flang = langIndex[p.lang];
        if (flang) {
          p.lang = flang.alpha2;
        }
      }
    });
  }
  private fixCountryCodes() {
    this.progEach(this.title);
    this.progEach(this.desc);
    this.progEach(this.subTitle);
  }
  // private programFilterLang(elm:any, lang:string){
  //     if (!elm) return null;
  //     return elm.filter((p:any)=>{
  //         if (p.lang && p.lang===lang){
  //             return true;
  //         } else {
  //             if (!p.lang) return true;
  //             return false;
  //         }
  //     });
  // }
  private programFilterLangs(elm: typeof this.desc) {
    return elm?.filter((p: any) => {
      if (langs().includes(p.lang)) {
        return true;
      } else {
        if (!p.lang) return true;
        return false;
      }
    });
  }
  private genDefaultProgramForLang(elm: typeof this.desc) {
    var lelm = elm?.filter((stitle) => {
      if (stitle.lang === i18().resolvedLanguage) return true;
      return false;
    });
    if (lelm && !lelm.length) {
      lelm = elm?.filter((stitle) => {
        if (!stitle.lang) return true;
        return false;
      });
      if (lelm && !lelm.length) {
        //     lelm.push({ _value: "No Program data"});
        return undefined;
      }
    }
    return lelm?.[0]._value;
  }
  getProgramInfo() {
    const ltitle = this.genDefaultProgramForLang(this.title);
    const ldesc = this.genDefaultProgramForLang(this.desc);
    const lsubTitle = this.genDefaultProgramForLang(this.subTitle);
    // console.log("Prog Info:",{title:ltitle,desc:ldesc,subTitle:lsubTitle});
    return {title: ltitle, desc: ldesc, subTitle: lsubTitle};
  }
  
}

export default Program;
