import Program from './Program';
import Programmes from './Programmes';

class Channel {
  id: string;
  name: string;
  tvgname: string;
  iconSrc: string | null;
  streamUrl: string | null;
  schedules: Programmes;
  currentProgram: Program|null;

  constructor(id: string, name: string, tvgname: string, iconSrc: string | null = null, streamUrl: string | null = null) {
    this.id = id;
    this.name = name;
    this.tvgname = tvgname;
    this.iconSrc = iconSrc;
    this.streamUrl = streamUrl;
    this.schedules = new Programmes();
    this.currentProgram = this.getCurrentProgram();
  }

  getCurrentProgram(): Program | null{
    return this.schedules.currentProgramm() ;
  }

  updateCurrentProgram(): void {
    this.currentProgram = this.getCurrentProgram();
  }
  getNextProgram(): Program | null {
    return this.schedules.getNextProgram();
  }
  isCurrent(channel: Channel | null): boolean {
    if (!channel) return false;
    if (this.id === channel.id) return true;
    return true;
  }
  getCleanChannelName() {
    return (
      this.name
      // Remove text inside [] or ()
      .replace(/(\[.*?\]|\(.*?\))/g, '')
      // Remove any word "TV"
      .replace(/\bHD\b/g, '')
      .replace(/\bTV\b/g, '')
      .replace(/\bhd\b/g, '')
      .replace(/\btv\b/g, '')
      // .replace(/\bChannel\b/g, '')
      // .replace(/\bchannel\b/g, '')      
      .trim()
      // Replace multiple spaces with a single space
      .replace(/\s+/g, ' ')
    );
  }
  getChannelName() {
    return this.getCleanChannelName() || this.name;
  }
  getChannelNameWithIcon() {
    return this.getChannelName() + (this.iconSrc ? ` (${this.iconSrc})` : '');
  }
  getChannelNameWithIconAndUrl() {
    return this.getChannelNameWithIcon() + (this.streamUrl ? ` (${this.streamUrl})` : '');
  }
  getChannelNameWithIconAndUrlAndTvgName() {
    return this.getChannelNameWithIconAndUrl() + (this.tvgname ? ` (${this.tvgname})` : '');
  }
  getChannelNameWithIconAndUrlAndTvgNameAndCurrentProgram() {
    return this.getChannelNameWithIconAndUrlAndTvgName() + (this.currentProgram?.getProgramInfo().title ? ` (${this.currentProgram.getProgramInfo().title})` : '');
  }  
}
export default Channel;
