import React, {useEffect} from 'react';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import ImageListItemBar from '@mui/material/ImageListItemBar';
import useTVChannels from './TVChannelsContextHook';
import Channel from '../classes/Channel';
import MiniPlayer from './MiniPlayer';

//a component that diplay a live grid of IPTV channels for the user to select. Scrollable, has Channel name and current program
export default function Videorama() {
  const {channels, isVideoPaused, isVROpen, device, setIsVideoPaused, switchChannelById, setIsVROpen} = useTVChannels();
  const videoClicked = (channel: Channel) => {
    switchChannelById(channel.id);
    setIsVideoPaused(false);
    setIsVROpen(false);
  };
  useEffect(() => {
    if (isVROpen && !isVideoPaused) setIsVideoPaused(true);
  }, [isVROpen, isVideoPaused, setIsVideoPaused]);

  if (!isVROpen) return <></>;

  return (
    <ImageList cols={device.md ? 2 : 1} sx={{width: '100%', height: '100%', position: 'absolute', top: '0px', left: '0px'}}>
      {channels
      .getAllChannels()
      .filter((c, i) => i < 100)
      .map((channel) => (
        <div key={'vr' + channel.id} onClick={() => videoClicked(channel)}>
          <ImageListItem>
            <MiniPlayer name={channel.name} url={channel.streamUrl || ''} />
            <ImageListItemBar
              sx={{
                background: 'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
              }}
              title={channel.name}
              subtitle={<span>{channel.currentProgram?.getProgramInfo().title}</span>}
              position='top'
            />
          </ImageListItem>
        </div>
      ))}
    </ImageList>
  );
}
